import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { MainLayoutComponent } from './main/main-layout/main-layout.component';
import { CreateWalletComponent } from './create-wallet/create-wallet.component';
import { WalletDashboardComponent } from './wallet-dashboard/wallet-dashboard.component';
import { AccessWalletComponent } from './access-wallet/access-wallet.component';
import { SwapComponent } from './swap/swap.component';

const routes: Routes = [
  { path: '', redirectTo: '/home', pathMatch: 'full' },
  { path: 'home', component: MainLayoutComponent },
  { path: 'create-wallet', component: CreateWalletComponent },
  { path: 'access-wallet', component: AccessWalletComponent },
  { path: 'dashboard', component: WalletDashboardComponent },
  { path: 'swap', component: SwapComponent },
  { path: '**', component: MainLayoutComponent }
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
