@Component({
  selector: 'dialog-overview-example-dialog',
  templateUrl: 'private-key-model.html',
  styleUrls: ['./access-wallet.component.css']
})
export class PrivateKeyData {

  mnemonic: Array<string>;
  display_mnemonic: Array<string>;

  @ViewChild('alrtDiv', { read: true, static: false }) public alrtDiv: ElementRef;

  constructor(public dialogRef: MatDialogRef<PrivateKeyData>,
    @Inject(MAT_DIALOG_DATA) public data: DialogData,
    private apiServices: ApiServices) { }

  onNoClick(): void {
    this.dialogRef.close();
  }

  submitWif(form: NgForm) {
    this.onNoClick();
    this.apiServices.importWithWif(form);
  }

  scrollTop() {
    setTimeout(() => {
      this.alrtDiv.nativeElement.scrollIntoView({ behavior: 'smooth', block: 'end', inline: 'start' });
    });
  }
}
