module.exports = ".wallet-data {\n    /* border-bottom: 1px solid #e0e0e0; */\n    color: #999;\n    font-size: 15px;\n    padding: 8px 0;\n}\n\n.pwd-txt {\n    font-size: 18px;\n    font-weight: 500;\n}\n\n.wall-header {\n    width: 100%;\n    background-color: #ffab00;\n    padding: 8px 4px;\n    color: #fff;\n    border-radius: 4px;\n}\n\n/* Custome Switch Box */\n\n.onoffswitch {\n    position: relative;\n    width: 90px;\n    -webkit-user-select: none;\n    -moz-user-select: none;\n    -ms-user-select: none;\n}\n\n.onoffswitch-checkbox {\n    display: none;\n}\n\n.onoffswitch-label {\n    display: block;\n    overflow: hidden;\n    cursor: pointer;\n    border: 2px solid #F5F5F5;\n    border-radius: 20px;\n}\n\n.onoffswitch-inner {\n    display: block;\n    width: 200%;\n    margin-left: -100%;\n    transition: margin 0.3s ease-in 0s;\n}\n\n.onoffswitch-inner:before,\n.onoffswitch-inner:after {\n    display: block;\n    float: left;\n    width: 50%;\n    height: 30px;\n    padding: 0;\n    line-height: 30px;\n    font-size: 14px;\n    color: white;\n    font-family: Trebuchet, Arial, sans-serif;\n    font-weight: bold;\n    box-sizing: border-box;\n}\n\n.onoffswitch-inner:before {\n    content: \"12\";\n    padding-left: 10px;\n    background-color: #F0BA09;\n    color: #FFFFFF;\n}\n\n.onoffswitch-inner:after {\n    content: \"24\";\n    padding-right: 10px;\n    background-color: #09C3E8;\n    color: #FFFFFF;\n    text-align: right;\n}\n\n.onoffswitch-switch {\n    display: block;\n    width: 22px;\n    margin: 6px;\n    background: #FFFFFF;\n    position: absolute;\n    top: 0;\n    bottom: 0;\n    right: 56px;\n    border: 2px solid #F5F5F5;\n    border-radius: 20px;\n    transition: all 0.3s ease-in 0s;\n}\n\n.onoffswitch-checkbox:checked+.onoffswitch-label .onoffswitch-inner {\n    margin-left: 0;\n}\n\n.onoffswitch-checkbox:checked+.onoffswitch-label .onoffswitch-switch {\n    right: 0px;\n}\n\n/* radio buttons custom design sky */\n\n/* The container */\n\n.cntr {\n    display: block;\n    position: relative;\n    padding-left: 35px;\n    margin-bottom: 12px;\n    cursor: pointer;\n    font-size: 18px;\n    -webkit-user-select: none;\n    -moz-user-select: none;\n    -ms-user-select: none;\n    user-select: none;\n}\n\n/* Hide the browser's default radio button */\n\n.cntr input {\n    position: absolute;\n    opacity: 0;\n    cursor: pointer;\n}\n\n/* Create a custom radio button */\n\n.checkmark {\n    position: absolute;\n    top: 0;\n    left: 0;\n    height: 25px;\n    width: 25px;\n    background-color: #eee;\n    border-radius: 50%;\n}\n\n/* On mouse-over, add a grey background color */\n\n.container:hover input~.checkmark {\n    background-color: #ccc;\n}\n\n/* When the radio button is checked, add a blue background */\n\n.cntr input:checked~.checkmark {\n    background-color: #fbbf10;\n}\n\n/* Create the indicator (the dot/circle - hidden when not checked) */\n\n.checkmark:after {\n    content: \"\";\n    position: absolute;\n    display: none;\n}\n\n/* Show the indicator (dot/circle) when checked */\n\n.cntr input:checked~.checkmark:after {\n    display: block;\n}\n\n/* Style the indicator (dot/circle) */\n\n.cntr .checkmark:after {\n    top: 9px;\n    left: 9px;\n    width: 8px;\n    height: 8px;\n    border-radius: 50%;\n    background: white;\n}\n\n.btn-warning {\n    background-color: #fbbf10;\n    /* color: #fff; */\n}\n\n.mtest_fm {\n    display: inline-block;\n    color: rgb(0, 57, 69);\n    width: calc(100% - 40px);\n    border-width: 0px;\n    border-style: initial;\n    border-color: initial;\n    -o-border-image: initial;\n    border-image: initial;\n    float: left;\n    padding: 10px 2px;\n}\n\n.mlabel {\n    width: 24px;\n    padding: 15px 1px;\n    float: left;\n}\n\n.wall_cbtn {\n    font-size: 12px;\n    background-color: #F0BA09;\n    color: #fff;\n    font-weight: 500;\n    text-transform: uppercase;\n    padding: 2px 10px;\n    cursor: pointer;\n    border-radius: 50px;\n    width: 168px;\n    text-align: center;\n    letter-spacing: 1.5px;\n    box-shadow: 0px 8px 15px rgba(0, 0, 0, 0.1);\n    transition: all 0.3s ease 0s;\n    cursor: pointer;\n    outline: none;\n    border: none;\n}\n\n.wall_cbtn:hover {\n    background-color: rgb(255, 188, 43);\n    box-shadow: 0px 15px 20px rgba(250, 222, 63, 0.4);\n    color: #fff;\n    -webkit-transform: translateY(-4px);\n            transform: translateY(-4px);\n}\n\n/* model css */\n/*# sourceMappingURL=data:application/json;base64, */"